.game {
    display: grid;
    grid-template-columns: [s sidebar-s] .1fr [sidebar-e colorboard-s] 1fr [colorboard-e pegboard-s] .3fr [pegboard-e e];
    grid-template-rows: 1fr;
    height: 93vh;

    &__sidebar {
      background-color: #4d4d4d;
      grid-column: sidebar-s/sidebar-e;
    }

    &__colorboard {
        background-color: blueviolet;
        grid-column: colorboard-s/colorboard-e;
        overflow: auto;
      }
    
    &__pegboard {
      background-color: #4d4d4d;
      grid-column: pegboard-s/pegboard-e
    }

    &__submit {
      grid-column: colorboard-s/colorboard-e;
      justify-self: right;
      margin-left: 1250px;
      border: none;
      background-color: #4d4d4d;
      color: white;
      height: 50px;
      width: 100px;
      border-radius: 5px;
      font-size: 20px;
      font-weight: bold;
    }
}



